import axiosInstance from "@/api/axios/axiosInstance";

export const getAllClinicalStudies = async () => {
  try {
    const response = await axiosInstance.get("/api/clinical-studies");
    return response.data;
  } catch (error) {
    console.error("Error fetching clinical studies:", error);
    throw error;
  }
};

export const getClinicalStudyById = async (id: string) => {
  try {
    const response = await axiosInstance.get(`/api/clinical-studies/${id}`);
    return response.data;
  } catch (error) {
    console.error("Error fetching clinical study:", error);
    throw error;
  }
};

export const createClinicalStudy = async (studyData: any) => {
  try {
    const response = await axiosInstance.post("/api/clinical-studies", studyData);
    return response.data;
  } catch (error) {
    console.error("Error creating clinical study:", error);
    throw error;
  }
};

export const updateClinicalStudy = async (id: string, studyData: any) => {
  try {
    const response = await axiosInstance.put(`/api/clinical-studies/${id}`, studyData);
    return response.data;
  } catch (error) {
    console.error("Error updating clinical study:", error);
    throw error;
  }
};

export const deleteClinicalStudy = async (id: string) => {
  try {
    const response = await axiosInstance.delete(`/api/clinical-studies/${id}`);
    return response.data;
  } catch (error) {
    console.error("Error deleting clinical study:", error);
    throw error;
  }
};

export const getClinicalStudyStats = async () => {
  try {
    const response = await axiosInstance.get("/api/clinical-studies/stats");
    return response.data;
  } catch (error) {
    console.error("Error fetching clinical study stats:", error);
    throw error;
  }
};

export const getStudyParticipants = async (studyId: string) => {
  try {
    const response = await axiosInstance.get(`/api/clinical-studies/${studyId}/participants`);
    return response.data;
  } catch (error) {
    console.error("Error fetching study participants:", error);
    throw error;
  }
};

export const assignStudyToSite = async (studyId: string, siteId: string, notes?: string) => {
  try {
    const response = await axiosInstance.post(`/api/clinical-studies/${studyId}/assign-site`, {
      site_id: siteId,
      notes: notes
    });
    return response.data;
  } catch (error) {
    console.error("Error assigning study to site:", error);
    throw error;
  }
};
